    font-size: 1.875rem;
    letter-spacing: 0.5px;
    color: #787878; }
  .contact .conact-array {
    display: flex;
    align-items: center;
    margin-top: 1em; }
    .contact .conact-array a {
      margin-right: 4.125rem; }
  .contact .icons {
